const CHANGE_TEXT = 'home/CHANGE_TEXT';

interface changeText {
  type: typeof CHANGE_TEXT;
  text: string;
}

type ActionTypes = changeText;

const changeText = (text: string = ''): ActionTypes => ({
  type: CHANGE_TEXT,
  text,
});

export const actionCreators = {
  changeText,
};

export interface IProps {
  text: string;
  changeText: Function;
}

export interface IState {
  text: string;
}

export const initialState: IState = {
  text: '',
};

export default function HomeScreen(
  state = initialState,
  action: ActionTypes | any,
): IState {
  switch (action.type) {
    case CHANGE_TEXT:
      return {
        ...state,
        text: action.text,
      };
    default:
      return state;
  }
}
